/*
* rightPad ()
*   s:, l:
*
* date2String 
*  d:, separator
* return 2019-05-14 17:25:45.359
*
* */
import store from '@/store'
let utils = {
  rightPad (s, l) {
    s += ''
    l = l || 2
    let sLength = s.length
    if (sLength == 1) {
      s = '0' + s
    } else {
      for (let i = sLength; i < l; i++) {
        s = '0' + s
      }
    }
    return s
  },
  date2String (d, separator) {
    let _this = this
    if (typeof d === 'string') {
      separator = d
      d = null
    }
    d = d || new Date(new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0))
    separator = separator || ''
    // 2019-05-27 
    return d.getFullYear().toString() + separator + _this.rightPad(d.getMonth() + 1) + separator + _this.rightPad(d.getDate())
  },
  time2String (d, separator) {
    let _this = this
    if (typeof d === 'string') {
      separator = d
      d = null
    }
    d = d || new Date(new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0))
    separator = separator || ''
    // 10:27:57 
    return _this.rightPad(d.getHours()) + separator + _this.rightPad(d.getMinutes()) + separator + _this.rightPad(d.getSeconds())
  },
  time2StringNotSec (d, separator) {
    let _this = this
    if (typeof d === 'string') {
      separator = d
      d = null
    }
    d = d || new Date(new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0))
    separator = separator || ':'
    // 10:27:57 
    return _this.rightPad(d.getHours()) + separator + _this.rightPad(d.getMinutes())
  },
  time2StringWithMs (d, separator) {
    let _this = this
    if (typeof d === 'string') {
      separator = d
      d = null
    }
    d = d || new Date(new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0))
    separator = separator || ''
    // 10:27:57.555 
    return _this.rightPad(d.getHours()) + separator + _this.rightPad(d.getMinutes()) + separator + _this.rightPad(d.getSeconds()) + (separator && separator.length ? '.' : '') + _this.rightPad(d.getMilliseconds(), 3)
  },
  generateTimeNumber (d, withMs) {
    let _this = this
    d = d || new Date(new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0))
    // 20190527102909 / 20190527102909555()
    return _this.date2String(d) + (!!withMs ? _this.time2StringWithMs(d) : _this.time2String(d))
  },
  generateTimeString (d, withMs) {
    let _this = this
    d = d || new Date(new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0))
    // 2019-05-27 10:27:57 / 2019-05-27 10:27:57.555
    return _this.date2String(d, '-') + ' ' + (!!withMs ? _this.time2StringWithMs(d, ':') : _this.time2String(d, ':'))
  },
  blob2Base64 (blob, callback) {
    let a = new FileReader()
    a.onload = function (e) {
      console.log(e.target.result)
      callback(e.target.result)
    }
    a.readAsDataURL(blob)
  },
  base64_2Blob (url) {
    let arr = url.split(','), mime = arr[0].match(/:(.*?);/)[1],
      bStr = atob(arr[1]), n = bStr.length, u8arr = new Uint8Array(n)
    while (n--) {
      u8arr[n] = bStr.charCodeAt(n)
    }
    return new Blob([u8arr], {type: mime})
  },
  base64_2File (base64, filename) {
    let _this = this
    let arr = base64.split(','),
      mime = arr[0].match(/:(.*?);/)[1],
      bStr = atob(arr[1]),
      n = bStr.length,
      u8arr = new Uint8Array(n)
    if (!filename) {
      filename = _this.generateTimeNumber()
    }
    filename += '.' + mime.split('/')[1]
    while (n--) {
      u8arr[n] = bStr.charCodeAt(n)
    }
    return new File([u8arr], filename, {type: mime})
  },
  image2Base64 (url, callback) {//Base64
    url = url.replace(/_thum/, "")
    let canvas = document.createElement('canvas'),
      ctx = canvas.getContext('2d'),
      img = new Image
    img.crossOrigin = 'Anonymous'
    img.onload = function () {
      canvas.height = img.height
      canvas.width = img.width
      ctx.drawImage(img, 0, 0)
      let dataURL = canvas.toDataURL('image/png')
      callback(dataURL)
      canvas = null
    }
    img.src = url
  },
  async arraySyncForeach (arr, callback) {
    const length = arr.length
    const O = Object(arr)
    let k = 0
    while (k < length) {
      if (k in O) {
        const kValue = O[k]
        await callback(kValue, k, O)
      }
      k++
    }
  },
  async arraySyncForeachReverse (arr, callback) {
    const length = arr.length
    const O = Object(arr)
    let k = length - 1
    while (k >= 0) {
      if (k in O) {
        const kValue = O[k]
        await callback(kValue, k, O)
      }
      k--
    }
  },
  // 
  getPagePosition (event, menuPosition, contextMenu) {
    const offsetH = document.documentElement.offsetHeight || document.body.offsetHeight
    const offsetW = document.documentElement.offsetWidth || document.body.offsetWidth
    if(contextMenu && (contextMenu.clientHeight + event.pageY > offsetH)){
      menuPosition.top = (event.pageY - contextMenu.clientHeight) + 'px'
    }else{
      menuPosition.top = event.pageY + 'px'
    }
    if(contextMenu && (contextMenu.clientWidth + event.pageX > offsetW)){
      menuPosition.left = (event.pageX - contextMenu.clientWidth) + 'px'
    }else{
      menuPosition.left = event.pageX + 'px'
    }
    return menuPosition
  },
  showKeyName (e) {
    let keyName
    switch (e.keyCode) {
      case 8:
        keyName = 'Backspace'
        break
      case 9:
        keyName = 'Tab'
        break
      case 13:
        keyName = 'Enter'
        break
      case 16:
        keyName = 'Shift'
        break
      case 17:
        keyName = 'Ctrl'
        break
      case 18:
        keyName = 'Alt'
        break
      case 19:
        keyName = 'PauseBreak'
        break
      case 20:
        keyName = 'Caps Lock'
        break
      case 27:
        keyName = 'Esc'
        break
      case 32:
        keyName = ''
        break
      case 33:
        keyName = 'PageUp'
        break
      case 34:
        keyName = 'PageDown'
        break
      case 35:
        keyName = 'End'
        break
      case 36:
        keyName = 'Home'
        break
      case 37:
        keyName = 'Left'
        break
      case 38:
        keyName = 'Up'
        break
      case 39:
        keyName = 'Right'
        break
      case 40:
        keyName = 'Down'
        break
      case 45:
        keyName = 'Insert'
        break
      case 46:
        keyName = 'Delete'
        break
      case 91:
        keyName = 'Win'
        break
      case 92:
        keyName = 'Win'
        break
      case 93:
        keyName = ''
        break
      case 95:
        keyName = 'Sleep'
        break
      case 96:
        keyName = '0'
        break
      case 97:
        keyName = '1'
        break
      case 98:
        keyName = '2'
        break
      case 99:
        keyName = '3'
        break
      case 100:
        keyName = '4'
        break
      case 101:
        keyName = '5'
        break
      case 102:
        keyName = '6'
        break
      case 103:
        keyName = '7'
        break
      case 104:
        keyName = '8'
        break
      case 105:
        keyName = '9'
        break
      case 106:
        keyName = '*'
        break
      case 107:
        keyName = '+'
        break
      case 109:
        keyName = '-'
        break
      case 110:
        keyName = '.'
        break
      case 111:
        keyName = '/'
        break
      case 112:
        keyName = 'F1'
        break
      case 113:
        keyName = 'F2'
        break
      case 114:
        keyName = 'F3'
        break
      case 115:
        keyName = 'F4'
        break
      case 116:
        keyName = 'F5'
        break
      case 117:
        keyName = 'F6'
        break
      case 118:
        keyName = 'F7'
        break
      case 119:
        keyName = 'F8'
        break
      case 120:
        keyName = 'F9'
        break
      case 121:
        keyName = 'F10'
        break
      case 122:
        keyName = 'F11'
        break
      case 123:
        keyName = 'F12'
        break
      case 144:
        keyName = 'NumLock'
        break
      case 145:
        keyName = 'ScrollLock'
        break
      case 186:
        keyName = ';'
        break
      case 187:
        keyName = '='
        break
      case 188:
        keyName = ','
        break
      case 189:
        keyName = '-'
        break
      case 190:
        keyName = '.'
        break
      case 191:
        keyName = '/'
        break
      case 192:
        keyName = '`'
        break
      case 219:
        keyName = '['
        break
      case 220:
        keyName = '\\'
        break
      case 221:
        keyName = ']'
        break
      case 222:
        keyName = '\''
        break
      //case 255:keyName = e.keyCode+"[Power]";break;
      case 255:
        keyName = 'Wake'
        break
      default:
        keyName = String.fromCharCode(e.keyCode)
        break
    }
    if ((e.shiftKey) && (e.keyCode != 16)) {
      keyName = 'Shift + ' + keyName
    }
    if ((e.altKey) && (e.keyCode != 18)) {
      keyName = 'Alt + ' + keyName
    }
    if ((e.ctrlKey) && (e.keyCode != 17)) {
      keyName = 'Ctrl + ' + keyName
    }
    return keyName
  },
  /*
  *
  *date:
  *return:true/false
  */
  SameWeek(date) {
    let date1 = new Date(date.replace(/-/g, "/"));         //
    let date2 = new Date(new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0));     //
    let curWeek = date2.getDay();           //
    let monday = this.GetDate((curWeek), 1); //
    let sunday = this.GetDate((7 - curWeek), 2); //
    if (date1.getTime() < monday.getTime() || date1.getTime() > sunday.getTime()) {
      return false;       //
    } else {
      return true;        //
    }
  },
  /*
  *
  *date:
  *return:true/false
  */
  SameMonth (date){
    let date1 = new Date(date.replace(/-/g, "/"));         //
    let date2 = new Date(new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0));        //
    let curDay = date2.getDate();           //
    let firstDay = this.GetDate((curDay), 1);                                //
    let lastDay = this.GetDate((this.getDaysInMonth(date2.getFullYear(), date2.getMonth() + 1) - curDay), 2);    //
    if (date1.getTime() < firstDay.getTime() || date1.getTime() > lastDay.getTime()) {
      return false;       //
    } else {
      return true;        //
    }
  },
  /*
  *
  */
  getDaysInMonth(year, month) {
    month = parseInt(month, 10) + 1;
    let temp = new Date(year + "/" + month + "/0");
    return temp.getDate();
  },
  /*
  *NN(N = day)
  *type:12
  */
  GetDate(day, type) {
    let zdate = new Date(new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0));
    let edate;
    if (type == 1) {
      edate = new Date(zdate.getTime() - (day * 24 * 60 * 60 * 1000));
    } else {
      edate = new Date(zdate.getTime() + (day * 24 * 60 * 60 * 1000));
    }
    return edate;
  },
  requestFile (url, successFun, errorFun) {
    let oAjax = null;
    if (window.XMLHttpRequest) {
      // code for IE7+, Firefox, Chrome, Opera, Safari
      oAjax = new XMLHttpRequest()
    } else {
      // code for IE6, IE5
      oAjax = new ActiveXObject('Microsoft.XMLHTTP')
    }
    // url = /^blob/.test(url) ? url : url.replace(/(https?:\/\/)([0-9a-z._]+)(:[0-9]+)?\/(accessFile\/)?/, window.CurrentUser.confMap['clientAuthResp_fileServer_Url'])
    oAjax.open('GET', url, true)
    oAjax.onreadystatechange = function () {
      if (oAjax.readyState == 4) {
        if (oAjax.status == 200) {
          successFun(oAjax.responseText)
        } else {
          errorFun(oAjax.status)
        }
      }
    }
    oAjax.send()
  },
  ReverseRankingDate(arr, str) {
    for (let i = 0; i < arr.length - 1; i++) {
        for (let j = 0; j < arr.length - 1 - i; j++) {
          console.log(arr[j][str]);
          if (arr[j][str] < arr[j+1][str]) {
              var temp = arr[j];
              arr[j] = arr[j + 1];
              arr[j + 1] = temp;
          }
      }
    }
    return arr;
  },
  //base64file
  dataURLtoFile (dataurl, filename) {
    let arr = dataurl.split(','),
      mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n)
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n)
    }
    return new File([u8arr], filename ? filename : '_' + new Date().getTime() + '.jpg', {type: mime})
  },
  // vue bus
  registerBusEvents (events, vm) {
    for (let name in events) {
      vm.$bus.off(name)
      vm.$bus.on(name, events[name])
    }
  },
}
export default {...utils}
